	toggleFullscreen() {
		const element =
			this.elementRef.nativeElement.querySelector("#fullscreenDiv");

		if (element.requestFullscreen) {
			element.requestFullscreen();
		} else if (element.mozRequestFullScreen) {
			element.mozRequestFullScreen();
		} else if (element.webkitRequestFullscreen) {
			element.webkitRequestFullscreen();
		} else if (element.msRequestFullscreen) {
			element.msRequestFullscreen();
		}

		// Update the fullscreen element
		this.fullscreenElement = this.getFullscreenElement();
	}

	checkFullscreenState() {
		const currentFullscreenElement = this.getFullscreenElement();
		this.fullScreen = currentFullscreenElement === this.fullscreenElement;
	}

