	checkOption(option: any, index1: number, index: number, val: number) {
		this.complianceOption = option;
		// console.log(index,"index is here")
		// console.log(this.tableData[this.selectedDomain]['data'][this.selectedSubDomain]['data'][index1]['Status'])
		this.tableData[this.selectedDomain]["data"][this.selectedSubDomain]["data"][
			index1
		]["Status"][index] = (100 - val * 50).toString();
		// this.assignStatusColor();
	}
	checkCriticality(option: any, index1: number, index: number, val: number) {
		this.criticalOption = option;
		this.tableData[this.selectedDomain]["data"][this.selectedSubDomain]["data"][
			index1
		]["Criticality"][index] = val.toString();
		// this.assignStatusColor();
	}
	setTextVal(
		k: any,
		index1: number,
		index: number,
		index2: number,
		val: string
	) {
		this.tableData[this.selectedDomain]["data"][this.selectedSubDomain]["data"][
			index1
		][k][index][index2] = val;
	}
	assignStatusColor() {
		if (
			this.complianceOption === "Compliant" ||
			(this.complianceOption === "Compliant" && this.criticalOption === "Low")
		) {
			this.classNameOption = "greenColor";
		} else if (
			this.complianceOption === "Partially Compliant" ||
			(this.complianceOption === "Partially Compliant" &&
				this.criticalOption === "Medium")
		) {
			this.classNameOption = "orangeColor";
		} else if (
			this.complianceOption === "Non Compliant" ||
			(this.complianceOption === "Non Compliant" &&
				this.criticalOption === "High")
		) {
			this.classNameOption = "redColor";
		}
		console.log(this.classNameOption);
	}

	changeDomain(i) {
		this.selectedDomain = i;
		this.selectedSection = 0;
		this.selectedSubDomain = 0;
	}
	changeSubDomain(i) {
		this.selectedSubDomain = i;
		this.selectedSection = 0;
	}
	changeSection(i) {
		this.selectedSection = i;
	}
	trackByFn(index: number, item: any) {
		return index; // or use a unique identifier of the item if available (e.g., item.id)
	}
	addElementAfterIndex(arr, index, element) {
		if (index >= 0 && index < arr.length) {
			arr.splice(index + 1, 0, element);
		} else {
			console.error("Index out of range!");
		}
	}
	removeElementAtIndex(arr, index) {
		if (index >= 0 && index < arr.length) {
			arr.splice(index, 1);
		} else {
			console.error("Index out of range!");
		}
	}

	onKeyPress(
		k: any,
		index1: number,
		index: number,
		index2: number,
		val: string,
		event: KeyboardEvent
	) {
		if (event.key === "Enter") {
			this.addElementAfterIndex(
				this.tableData[this.selectedDomain]["data"][this.selectedSubDomain][
				"data"
				][index1][k][index],
				index2,
				""
			);
		}
		if (
			this.tableData[this.selectedDomain]["data"][this.selectedSubDomain][
				"data"
			][index1][k][index].length === 1
		)
			return;
		if (
			event.key === "Backspace" &&
			this.tableData[this.selectedDomain]["data"][this.selectedSubDomain][
				"data"
			][index1][k][index].length > 1
		) {
			if (val == "") {
				this.removeElementAtIndex(
					this.tableData[this.selectedDomain]["data"][this.selectedSubDomain][
					"data"
					][index1][k][index],
					index2
				);
			}
		}
	}
