import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './main/main.component';
import {  RiskRegister} from './RiskRegister/RiskRegister.component';

import { Dashboard2Component } from './dashboard2/dashboard2.component';
import { Dashboard3Component } from './dashboard3/dashboard3.component';
import { IntersectionAITPRMComponent } from './Intersection/Intersection.component';

import { FormListComponent } from './FormList/FormList.component';
import {AItechComponent  } from './AITECHNOLOGY/AItech.component';
import { ProcessComponent } from './AIPROCESS/process.component';
import { CassesComponent } from "./casses/casses.component";
import { PassesComponent } from "./passes/passes.component";
import { ComplianceAssesmentComponent } from "./compliance-assesment/compliance-assesment.component";

const routes: Routes = [
  {
    path: '',
    redirectTo: 'riskregister',
    pathMatch: 'full'
  },

  {
    path: 'report',
    component: IntersectionAITPRMComponent
  },

  {
    path: 'AItech',
    component: AItechComponent
  },

  {
    path: 'Process',
    component: ProcessComponent
  },

  {
    path: 'FormList',
    component: FormListComponent
  },

  {
    path: 'riskregister',
    component: RiskRegister
  },

  {
    path: 'scoping/assessment',
    component: MainComponent
  },

  {
		path: "compAssess",
		component: ComplianceAssesmentComponent,
	},
	{
		path: "casses",
		component: PassesComponent,
	},
	{
		path: "passes",
		component: CassesComponent,
	},

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TprmRoutingModule {}
